import React from "react";

import ShareManager from "./ShareManager";

var DownloadManager = React.createClass({
    render: function () {
        return (
            <div className="actions-wrapper action-btn">

                <div className="download-dropdown">
                    <a><span>{langClip.download}</span></a>
                    <ul className="dropdown-menu">
                        <li><a id="download-clip-filter">{langClip.csv}</a></li>
                        <li>asdfdsf</li>
                    </ul>
                </div>
                <div id="print-clip-filter">{langClip.printClip}</div>
                <div className="modal-social-share-wrap social-share">
                    <div>
                        <div id="save-clipping" style={{width:'135px'}}>{langClip.save_and_shareClip}</div>
                        <ShareManager/>
                    </div>
                </div>
            </div>
        );
    }
});

module.exports = DownloadManager;


